body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root{
  position: relative;
  width: 100%;
  height: 100%;
}

#headerTop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 48px;
  line-height: 48px;
  box-sizing: border-box;
  background-color: #0B72C0;
}
#headerTop h2 {
  padding-left: 20px;
  color: #fff;
}
#headerTop .ant-dropdown-link {
  position: absolute;
  right: 20px;
  top: 0;
  bottom: 0;
  margin: auto;
  color: #fff;
}
#headerTop .ant-dropdown-link img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 10px;
}

#leftNav {
  position: fixed;
  width: 140px;
  height: 100%;
  left: 0;
  top: 48px;
  z-index: 9;
}
#leftNav ul.ant-menu {
  height: 100%;
}

#appContent {
  height: 100vh;
  box-sizing: border-box;
  padding: 48px 0 0 140px;
}
#loadingBox {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.7);
  left: 0;
  top: 0;
}
#loadingBox .ant-spin {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -10px;
  margin-top: -12.5px;
}

.loginBox {
  display: table;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  background-size: cover;
  background-position: center;
  background-image: url("http://static.yishengyue.cn/index_03.jpg");
}
.loginBox .loginBg{
  width: 100%;
  height: 100%;
}
.loginBox .login {
  display: table-cell;
  vertical-align: middle;
}
.loginBox .login .box {
  width: 440px;
  margin: 0 auto;
  padding: 20px 50px;
  border: 1px solid #efefef;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  background-color: #fff;
}
.loginBox .login .box h2 {
  text-align: center;
  font-size: 18px;
  line-height: 50px;
}
.loginBox .login .box .ant-btn-primary {
  width: 100%;
}

.homeBox {
  padding: 30px;
}
.homeBox .contentBox {
  box-shadow: 0 0 15px #999;
  border-radius: 7px;
  padding: 0 10px;
}
.homeBox .contentBox .titleBox {
  text-align: right;
  padding: 20px 0;
}
.inputNum {
