}
.company .contentBox .companyText {
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  /*! autoprefixer: off */
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
}
.company .contentBox .officeText {
  max-width: 200px;
  word-wrap: break-word;
}
.company .contentBox .companyLogo {
