.menuBox {
  padding: 30px;
}
.menuBox .contentBox {
  box-shadow: 0 0 15px #999;
  border-radius: 7px;
  padding: 0 10px;
}
.menuBox .contentBox .titleBox {
  padding: 20px 0;
  overflow: hidden;
}
.menuBox .contentBox .titleBox .ant-btn {
  float: right;
  margin-left: 24px;
}
.menuBox .contentBox .titleBox .leftTitle,
.menuBox .contentBox .titleBox .nextTitle {
  display: inline-block;
  height: 32px;
  line-height: 32px;
  font-size: 13px;
  color: #6b6b6b;
  background-color: #E6EDF3;
  text-align: center;
  position: relative;
  margin-right: 10px;
  padding-right: 20px;
  padding-left: 30px;
  z-index: 2;
}
.menuBox .contentBox .titleBox .nextTitle {
  z-index: 1;
}
.menuBox .contentBox .titleBox .nextTitle::before {
  content: "";
  width: 0;
  height: 0;
  left: 0;
  top: 0;
  border-width: 16px 0 16px 16px;
  border-style: solid;
  border-color: transparent transparent transparent #fff;
  /*透明 透明 透明 灰*/
  position: absolute;
}
.menuBox .contentBox .titleBox .leftTitle::after,
.menuBox .contentBox .titleBox .nextTitle::after {
  content: "";
  width: 0;
  height: 0;
  right: -16px;
  top: 0;
  border-width: 16px 0 16px 16px;
  border-style: solid;
  border-color: transparent transparent transparent #E6EDF3;
  /*透明 透明 透明 灰*/
  position: absolute;
}
.menuBox .linkNav {
  color: #1890ff;
  padding-bottom: 1px;
  cursor: pointer;
  border-bottom: 1px solid #1890ff;
}
